import React, { useState, useEffect } from 'react';
import { Layout, Input, Button, Avatar, Space, List, Card, Typography, Spin } from 'antd';
import { RobotOutlined, UserOutlined, PlusOutlined, LoadingOutlined } from '@ant-design/icons';
import styles from './index.less';
import { DefaultThinkingText, GetAgentResponses, GetMessagePreview, GetResponseReactNode, InitThinkingText, Message } from './chatScript';
import { Scenario, SCENARIO_STORAGE_KEY } from '@/contexts/constants';
import Typewriter from '@/components/Typewriter';

const { Content, Sider } = Layout;
const { TextArea } = Input;
const { Title } = Typography;

interface ChatSession {
  id: number;
  title: string;
  lastMessage: string;
  timestamp: string;
  messages: Message[];
}

const USER_CHAT_STORAGE_KEY = 'user_chat_sessions';

const ChatPage: React.FC = () => {
  // 从 localStorage 加载会话数据，如果没有则使用默认值
  const [sessions, setSessions] = useState<ChatSession[]>(() => {
    const savedSessions = localStorage.getItem(USER_CHAT_STORAGE_KEY);
    if (savedSessions && savedSessions !== '[]') {
      return JSON.parse(savedSessions);
    }
    return [{
      id: 1,
      title: 'New Chat',
      lastMessage: '',
      timestamp: new Date().toLocaleString(),
      messages: [],
    }];
  });
  const [currentSessionId, setCurrentSessionId] = useState<number>(1);
  const [inputValue, setInputValue] = useState('');
  const [inputDisabled, setInputDisabled] = useState(false);
  const [comparisonDetailFlag, setComparisonDetailFlag] = useState(false);
  const [orderDetailFlag, setOrderDetailFlag] = useState(true);

  // 当会话数据更新时，保存到 localStorage
  useEffect(() => {
    localStorage.setItem(USER_CHAT_STORAGE_KEY, JSON.stringify(sessions));

    const scrollableArea = document.getElementById('scrollableArea');
    if (scrollableArea) {
      scrollableArea!.scrollTop = scrollableArea!.scrollHeight;
    }
  }, [sessions]);

  const currentSession = sessions.find(session => session.id === currentSessionId);

  const handleSendMessage = async () => {
    if (!inputValue.trim() || !currentSession) return;

    // disable input until the agent finishes responding
    setInputDisabled(true);

    // append user message to the session
    const newUserMessage: Message = {
      id: currentSession.messages.length + 1,
      content: inputValue,
      sender: 'user',
      timestamp: new Date().toLocaleString(),
      loading: false,
    };
    let updatedSessions = updateMessageToSession(newUserMessage, sessions);
    setInputValue('');

    const newAgentMessages = GetAgentResponses(newUserMessage);
    for (const message of newAgentMessages) {
      // thinking
      if (message.loading === true) {
        updateMessageToSession(message, updatedSessions);
        // wait for 1 second
        await new Promise(resolve => setTimeout(resolve, message.loadingTime || 1000));
      }

      // finish thinking
      message.loading = false;
      updatedSessions = updateMessageToSession(message, updatedSessions);

      // order confirmed
      if (message.content === '44444') {
        localStorage.setItem(SCENARIO_STORAGE_KEY, Scenario.orderReceived.toString());
        // setTimeout(() => {
        //   window.location.href = '/admin';
        // }, 5000)
      }
    }

    setInputDisabled(false);
  };

  const handleNewChat = () => {
    const newSession: ChatSession = {
      id: sessions.length + 1,
      title: `New Chat ${sessions.length + 1}`,
      lastMessage: '',
      timestamp: new Date().toLocaleString(),
      messages: [],
    };
    setSessions([...sessions, newSession]);
    setCurrentSessionId(newSession.id);
  };

  const updateMessageToSession = (newMessage: Message, sessions: ChatSession[]) => {
    const updatedSessions = sessions.map(session => {
      if (session.id === currentSessionId) {
        return {
          ...session,
          messages: [...session.messages, newMessage],
          lastMessage: newMessage.loading ? session.lastMessage : newMessage.content,
          timestamp: newMessage.timestamp,
        };
      }
      return session;
    });

    setSessions(updatedSessions);
    return updatedSessions;
  }

  const isNewSession = currentSession && currentSession.messages.length === 0;

  return (
    <Layout className={styles.chatLayout}>
      <Sider width={160} className={styles.sider}>
        <div className={styles.siderHeader}>
          <Button
            type="text"
            icon={<PlusOutlined />}
            onClick={handleNewChat}
            className={styles.newChatButton}
          >
            New Chat
          </Button>
        </div>
        <div className={styles.siderContent}>
          <Title level={5} className={styles.siderContentTitle}>Past Sessions</Title>
          <List
            className={styles.sessionList}
            dataSource={sessions}
            renderItem={(session) => (
              <List.Item style={{ padding: "4px 0" }}>
                <Card
                  size="small"
                  className={`${styles.sessionCard} ${session.id === currentSessionId ? styles.activeSession : ''}`}
                  onClick={() => setCurrentSessionId(session.id)}
                >
                  <Space direction="vertical" style={{ width: '100%' }}>
                    <div className={styles.sessionTitle}>{session.title}</div>
                    <div className={styles.sessionMessage}>{GetMessagePreview(session.lastMessage)}</div>
                    <div className={styles.sessionTime}>{session.timestamp}</div>
                  </Space>
                </Card>
              </List.Item>
            )}
          />
        </div>
      </Sider>
      <Content className={styles.content}>
        <div className={styles.chatWindow} style={
          isNewSession ? { background: '#fff' } : { backgroundImage: `url(${require('./healthcare_bg.png')})` }
        }>
          {isNewSession && (
            <div className={styles.newSessionTitle}>Welcome! Tell me what you want~</div>
          )}
          {!isNewSession && (
            <div className={styles.messages} id={'scrollableArea'}>
              {currentSession?.messages.map((message) => (
                <div
                  key={message.id}
                  className={`${styles.message} ${message.sender === 'user' ? styles.userMessage : styles.agentMessage
                    }`}
                >
                  <Avatar
                    icon={message.sender === 'user' ? <UserOutlined /> : <RobotOutlined />}
                    className={styles.avatar}
                  />
                  <div className={styles.messageContent}>
                    {message.loading ? (
                      <div className={styles.messageText}>
                        <Spin indicator={<LoadingOutlined spin />} className={styles.messageLoading} />
                        <span className={styles.messageLoading}>{
                          message.content === '11111'
                            ? <Typewriter
                              text={InitThinkingText}
                              typingSpeed={2}
                            />
                            : DefaultThinkingText
                        }</span>
                      </div>
                    ) : (
                      <div className={styles.messageText}>
                        {message.content === '11111' && (
                          <>
                            <span className={styles.messageLoading}>{InitThinkingText}</span>
                            <div className={styles.messageDivider}></div>
                          </>
                        )}
                        {message.content === '22222'
                          ? <div>
                            {GetResponseReactNode(message.content, comparisonDetailFlag, setComparisonDetailFlag)}
                          </div>
                          : message.content === '33333'
                            ? <div>
                              {GetResponseReactNode(message.content, orderDetailFlag, setOrderDetailFlag)}
                            </div>
                            : GetResponseReactNode(message.content, false, () => { })
                        }
                      </div>
                    )}
                    <div className={styles.messageTime}>{message.timestamp}</div>
                  </div>
                </div>
              ))}
            </div>
          )}
          <div className={styles.inputArea}>
            <div className={styles.textAreaWrapper}>
              <TextArea
                value={inputValue}
                className={styles.textArea}
                onChange={(e) => setInputValue(e.target.value)}
                placeholder="Type your message..."
                autoSize={{ minRows: 2, maxRows: 6 }}
                onPressEnter={(e) => {
                  if (!e.shiftKey) {
                    e.preventDefault();
                    handleSendMessage();
                  }
                }}
              />
              <Button
                type="primary"
                onClick={handleSendMessage}
                className={styles.sendButtonInTextArea}
                disabled={inputDisabled}
              >
                Send
              </Button>
            </div>
          </div>
          {isNewSession && (
            <div className={styles.newSessionProductList}>
              <div className={styles.productItem}>
                <img src={require('./healthcare.png')} alt="Healthcare" className={styles.productImage} />
                <div className={styles.productName}>Healthcare</div>
              </div>
              <div className={styles.productItem}>
                <img src={require('./education.png')} alt="Education" className={styles.productImage} />
                <div className={styles.productName}>Education</div>
              </div>
              <div className={styles.productItem}>
                <img src={require('./automobile.png')} alt="Automobile" className={styles.productImage} />
                <div className={styles.productName}>Automobile</div>
              </div>
            </div>
          )}
        </div>
      </Content>
    </Layout>
  );
};

export default ChatPage; 